import React,{useState} from 'react' 
import {graphql, navigate} from 'gatsby'
import Tree from 'react-d3-tree'
import * as styles from './styles.module.css'
import "rc-tree/assets/index.css"
import {remarksToTree, useCenteredTree, addKeyToTree, bfs} from '../script/tree'
import Navbar from '../components/Navbar'
import Img from 'gatsby-image'
import {default as TreeMenu} from 'rc-tree';

function IndexPage({data, pageContext, path, ...others}){
  console.log(data)
  console.log(others)
  console.log(pageContext)
  const tree = bfs(
    addKeyToTree(
      remarksToTree(data.allMarkdownRemark.nodes, pageContext.langKey)
    ),
    node=>{
      node.title = node.name
    }
  )
  const [translate, containerRef] = useCenteredTree();
  console.log(tree)
  return (<div className={styles.container} ref={containerRef}>
      <Navbar lang={pageContext.langKey} path={path}/>
      <Img fluid={data.file.childImageSharp.fluid} className={styles.bg} style={{position:"absolute"}}></Img>
      <TreeMenu 
        showIcon={false}
        onExpand={(data)=>{console.log(data)}}
        className={styles.menu}
        treeData={tree.children}
        defaultExpandedKeys={tree.children.map(child=>child.key)}
        height = {200}
        itemHeight = {20}
        switcherIcon = {obj => {
          if(obj.expanded){
            
          }
        }}
      />
      <div className={styles.tree}>
        <Tree data={tree}
            rootNodeClassName={styles.root}
            branchNodeClassName={styles.branch}
            leafNodeClassName={styles.leaf}
            translate={translate}
            draggable={true}
            collapsible={false}
            onNodeMouseOver={(node)=>{console.log(`mouse over`);console.log(node)}}
            nodeSize={{x:200, y:200}}
            onNodeClick={(node)=>{
                var url = node.data.dirname === "" ? node.data.slug : node.data.dirname + "/" + node.data.slug
                navigate( url )
            }}
        />
      </div>
     
  </div>)
}

export const query = graphql`
query IndexPageQuery {
  file(relativePath: {eq: "background.jpg"}) {
    childImageSharp {
      fluid {
        base64
        tracedSVG
        srcWebp
        srcSetWebp
        originalImg
        originalName
        aspectRatio
        src
        srcSet
      }
    }
  }
  allMarkdownRemark {
    nodes {
      fileAbsolutePath
      frontmatter {
        title
      }
    }
  }
}
`

export default IndexPage